import React, { useState } from 'react';
import usePagination, { DOTS } from './usePagination';

export default function Pagination(props) {
    const { currentPage, totalCount, pageSize, onPageChange, siblingCount = 1 } = props
    const { rangeArray } = usePagination({
        totalCount, pageSize, currentPage, siblingCount
    })
    function changePageNumber(pageNumber) {
        onPageChange(pageNumber)

    }

    return (
        <ul className="page-numbers-container d-flex w-100 justify-content-around mt-5">
            {rangeArray && rangeArray.map(item => <li className={(currentPage === item) ? "number-page-active" : "number-page-inactive"} onClick={() => changePageNumber(item)} key={item}>{item}</li>)}
        </ul>
    )
}