import React, { useEffect, useState } from 'react';

const DOTS = "..."

export default function usePagination(
    {
        totalCount,
        pageSize,
        currentPage,
        siblingCount = 1
    }
) {
    const [rangeArray, setRangeArray] = useState()

    useEffect(() => {

        const totalPageCount = Math.ceil(totalCount / pageSize);

        const range = (start, end) => {
            let arrayLength = end - start + 1;
            return Array.from({ length: arrayLength }, (_, index) => index + start)
        }
        const totalPageNumber = siblingCount + 5;
        if (totalPageNumber >= totalPageNumber) {
            return setRangeArray(() => range(1, totalPageCount));
        }

        /*  const leftSiblingIndex = Math.max(currentPage - siblingCount, 1);
         const rightSiblingIndex = Math.min(currentPage + siblingCount, totalPageCount);
 
         const shouldShowLeftDots = leftSiblingIndex > 2;
         const shouldShouwRightDots = rightSiblingIndex < totalPageCount - 2;
 
         const firstPageIndex = 1;
         const lastPageIndex = totalPageCount;
 
         if (shouldShowLeftDots && shouldShouwRightDots) {
             let middleRange = range(leftSiblingIndex, rightSiblingIndex);
             return [firstPageIndex, DOTS, ...middleRange, DOTS, lastPageIndex]
         } */


    }, [totalCount, pageSize, siblingCount, currentPage])
    return { rangeArray }

};



export { DOTS }